@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap');
/*
#################################################
This is part of the UI framework being developed.
#################################################
*/
//---------
$gray0: #F6F8FF;
$transitin: all .3s;
$marginTabs: 20px;
$widthTabs: 110px;

$primary: #636983;
$success: #51a14c;
$default: #525252;
$warning: #b12222;

@function calcLeft($numTab, $width) {
  @return ($numTab - 1) * ( $width + 3 );
}

@mixin tabSuccess {
  & + label {
    color: $success;
  }
  & ~ .glider {
    background: linear-gradient(90deg, #51a14c 0%, #10c33e 100%);
    box-shadow: 0px 0px 8px 0px rgb(47 187 12 / 62%);
  }
}

@mixin tabDefault {
  & + label {
    color: $default;
  }
  & ~ .glider {
    background: linear-gradient(90deg, #4a4a4a 0%, #9b9b9b 100%);
  }
}

@mixin tabWarning {
  & + label {
    color: $warning;
  }
  & ~ .glider {
    background: linear-gradient(90deg, #b93232 0%, #ff5d5d 100%);
    box-shadow: 0px 0px 8px 0px rgb(231 13 93 / 57%);
  }
}

.title {
  text-align:center;
  padding: 40px 0;
  font-size: 40px;
  font-weight: 300;
  color: $primary;
}

.wrapper {
  background-color: $gray0;
  width: 100%;
  min-height: 100vh;
  
  .line {
    width: 50%;
    margin: 50px auto 0 auto;
    height: 1px;
    background-color: #c3d2e3;
  }
}

h2.tab-title {
  font-weight: 300;
  
  &.tab-primary {
    color: $primary;
  }
  
  &.tab-success {
    color: $success;
  }
  
  &.tab-default {
    color: $default;
  }
  
  &.tab-warning {
    color: $warning;
  }
}

.container {
  width: 100%;
  padding: 10px 0;
  text-align: center;
  
  .card {
    max-width: 600px;
    min-height: 140px;
    background-color: white;
    border-radius: 10px;
    border: 1px solid #e3e3e3;
    margin: 0 auto;
  }
}

.tabs {
  text-align: left;
  position: relative;
  
  input {
    position: absolute;
    opacity: 0;
    margin: 0;
    padding: 0;
  }
  input + label {
    font-weight: 300;
    color: #666;
    margin: 10px 0;
    width: $widthTabs;
    cursor: pointer;
    display:inline-block;
    transition: $transitin;
    text-align: center;
    font-size: 12px;
    border: none;
    &:hover {
      color: #777;
    }
  }
  
  .tabsShadow {
    width: 100%;
    padding: 10px;
    box-shadow: 0 0 25px 0 rgb(0 0 0 / 4%);
    box-sizing: border-box;
    position: absolute;
    height: 35px;
    z-index: 0;
    top: 0;
    pointer-events: none;
  }
}

.content {
  margin: 0px auto;
  text-align: left;
  color: #666;
  font-size: 13px;
  line-height: 20px;
  position: relative;
  height: 220px;
  
  .item {
    opacity: 0;
    visibility: hidden;
    transition: all .3s;
    position: absolute;
    padding: 10px 20px;
  }
  
  p {
    margin: 10px 0;
  }
}

.glider {
  width: $widthTabs;
  height: 3px;
  background: linear-gradient(90deg, rgba(91,60,196,1) 0%, rgba(60,108,196,1) 100%);
  border-radius: 3px;
  position: absolute;
  box-shadow: 0px 0px 8px 0px rgba(91, 60, 196, 0.4);
  transition: $transitin;
}

#tab-1:checked ~ .glider{
  left: calcLeft(1, $widthTabs);
}

#tab-2:checked {
  @include tabSuccess;
  & ~ .glider {
    left: calcLeft(2, $widthTabs);
  } 
}

#tab-3:checked {
  @include tabDefault;
  & ~ .glider{
    left: calcLeft(3, $widthTabs);
  }
}

#tab-4:checked {
  @include tabWarning;
  & ~ .glider{
    left: calcLeft(4, $widthTabs);
  }
}

#tab-1:checked ~ section #content-1,
#tab-2:checked ~ section #content-2,
#tab-3:checked ~ section #content-3,
#tab-4:checked ~ section #content-4 {
  opacity: 1;
  visibility: visible;
}

@media (max-width: 700px) {
  $widthTabs: 70px;
  
  .tabs input + label, .glider{
    width: $widthTabs;
  }
  
  #tab-1:checked ~ .glider{
    left: calcLeft(1, $widthTabs);
  } 
  
  #tab-2:checked ~ .glider{
    left: calcLeft(2, $widthTabs);
  } 
  
  #tab-3:checked ~ .glider{
    left: calcLeft(3, $widthTabs);
  }
  
  #tab-4:checked ~ .glider{
    left: calcLeft(4, $widthTabs);
  } 
}